/*  Weekly Challenge Hero hero banner
*
*  Version:
*    1.0.0 - 2020/12/23
*/
@import '../base.scss';

.c-weekly-challenge-hero {
  position: relative;
  padding-top: 8.125rem;
  z-index: 0;

  &__header-background {
    position: absolute;
    left: 0;
    width: 100%;
    top: 0;
    padding-bottom: 28.25rem;
    background-color: $s-color--background-tertiary;
    z-index: -1;
  }

  &__inner {
    padding-left: 1rem;
    padding-right: 1rem;
  }

  &__title {
    color: $s-color--font-6;
    margin-bottom: 3.6875rem;
    text-align: center;

    strong {
      font-weight: inherit;
      color: $s-color--dark-blue;
    }
  }

  &__content {
    color: $s-color--font-8;
    max-width: 35rem;

    &.is-desktop {
      display: none;
    }

    &.is-mobile {
      margin: 2.375rem auto 1.25rem;
      text-align: center;
      width: 89%;
      line-height: 1.125rem;
    }
  }

  &__image {

    &-container {
      padding-bottom: 128%;
    }
  }

  &__image-wrapper {
    position: relative;
  }

  &__image-overlay {
    @include t-absolute-center-x-y();
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(4.625rem);
    width: 80%;
    height: 84%;

    &-content {
      @include t-absolute-center-x-y();
    }

    &-title,
    &-subtitle {
      color: $s-color--font-6;
      text-align: center;
    }

    &-subtitle {
      margin-top: 1.75rem;
    }
  }

  @media screen and (min-width: 34.375rem) {
    &__image {

      &-container {
        padding-bottom: 85%;
      }
    }
  }

  @include t-media-query($s-small-down) {

    &__header {
      &-inner {
        margin: 0 auto;
        max-width: 25rem;
      }
    }

    &__image {
      &.is-desktop {
        display: none;
      }
    }
  }

  @include t-media-query($s-small-up) {
    &__header-background {
      padding-bottom: 20.5625rem;
    }

    &__inner {
      @include t-gutters(true);
      display: flex;
      justify-content: space-between;
    }

    &__heading {
      width: 40%;
      margin-top: 2.6875rem;
    }

    &__title {
      text-align: left;
      margin-bottom: 5.0625rem;
      width: 60%;
    }

    &__content {

      &.is-desktop {
        display: block;
      }

      &.is-mobile {
        display: none;
      }
    }

    &__images {
      width: 50%;
    }

    &__image {
      &.is-mobile {
        display: none;
      }

      &.is-desktop {
        display: block;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__header-background {
      padding-bottom: 33.6875rem;
    }

    &__inner {
      margin-top: 3.125rem;
    }

    &__title {
      margin-top: 3.75rem;
    }
  }

  @include t-media-query($s-large-up) {
    &__header-background {
      padding-bottom: 36.75rem
    }

    &__title {
      margin-top: 6rem;
      margin-bottom: 10.875rem;
    }

    &__content {
      line-height: 1.5625rem;
    }

    &__inner {
      margin-top: 4.6875rem;
    }

    &__images {
      width: 50.5%;
    }

    &__image {

      &-container {
        padding-bottom: 103%;
      }
    }

    &__image-overlay-subtitle {
      margin-top: 7.875rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__header-background {
      padding-bottom: 46.375rem;
    }

    &__title {
      margin-top: 8.6875rem;
      margin-bottom: 10.875rem;
    }
  }
}